import axios from 'axios';
const BRIDGE_URL = "https://m-bridge.incar.co.kr/IMO/Request.php";
const BRIDGE_URL_FILE = "https://m-bridge.incar.co.kr/IMO/FileDown.php";

export async function getPost(postData, base_url = BRIDGE_URL){
    let res = null;

    if(navigator.connection.type === 'none'){
        res = {data : {status :{ercode : '101', ermsg : '인터넷 연결을 확인 하세요.(Code:101)'}}};
        return res;
    }

    const params = new URLSearchParams();
    for(let key in postData){
        params.append(key, postData[key]);
    }
    if(window.localStorage.getItem('auth_key')){
        params.append("auth_key", window.localStorage.getItem('auth_key'));
    }
    //window.SpinnerPlugin.activityStart("데이터를 불러오는 중입니다.");
    try{
        res = await axios.post(
            base_url, 
            params , 
            {
                headers: {'Content-Type': 'application/x-www-form-urlencoded', 'Accept': '*/*'}, 
                timeout : 15000
            }
        );
        //window.SpinnerPlugin.activityStop();
        return res;
    }catch(error){
        //window.SpinnerPlugin.activityStop();
        res = {data : {status :{ercode : '100', ermsg : '서버에 오류가 발생 하였습니다.(Code:100)'}}};
        return res;
    }
}
export async function getFileDown(postData){
    return await getPost(postData,BRIDGE_URL_FILE);
}