function getDataToArr(rec, res_obj_value){
    let res_array = [];
    if(rec.length > 0 ){
        for(let rec_obj in rec){
            let __obj = {};
            for(let rec_key in rec[rec_obj]){
                if(res_obj_value){
                    for(let res_obj_key of res_obj_value){
                        if(rec_key === res_obj_key){
                            if(typeof rec[rec_obj][res_obj_key] === 'object' && Object.keys(rec[rec_obj][res_obj_key]).length === 0){
                                __obj[res_obj_key] = '';
                            }else{
                                __obj[res_obj_key] = rec[rec_obj][res_obj_key];
                            }                            
                        }
                    }
                }else{
                    if(typeof rec[rec_obj][rec_key] === 'object' && Object.keys(rec[rec_obj][rec_key]).length === 0){
                        __obj[rec_key] = '';
                    }else{
                        __obj[rec_key] = rec[rec_obj][rec_key];
                    }
                }
            }
            res_array.push(__obj);
        }
    }
    return res_array;
}

export const number_format = (number) =>{
    if(number) {
        var nArr = String(number).split('').join(',').split('');
        for( var i=nArr.length-1, j=1; i>=0; i--, j++)  if( j%6 !== 0 && j%2 === 0) nArr[i] = '';
        return nArr.join('')
    } else {
        return '0';
    }
}

export const recDataToArray = (rec, res_obj_value = null) =>{
    if(Array.isArray(rec)){
        return getDataToArr(rec, res_obj_value);
    }else if(typeof rec === 'object'){
         let __rec = [];
        if(rec){
            __rec[0] = rec;
            return getDataToArr(__rec, res_obj_value);
        }else{
            return __rec;
        }
    }
}
