/* eslint-disable react-hooks/exhaustive-deps */
/**완전판매확인서 */
import React, {useState, useEffect, createRef, useRef} from 'react';
import { useHistory } from "react-router-dom";
import {Button, Flex, Modal, WhiteSpace, Card, Checkbox, Toast} from "antd-mobile";
import { MainLayout } from 'styleComponents/MainLayout';
import RadioYesNo  from 'styleComponents/RadioYesNo';
import 'antd-mobile/dist/antd-mobile.css';
import moment from "moment";
import * as service from 'service/post';
import { useScreenshot } from "use-react-screenshot";

const alert = Modal.alert;
const fledDivStyle3 = {
    display : 'flex',
    height :'30px',
    justifyContent: 'center',
    alignItems: 'center'
}

export default function PerfectSale(props){
    const history = useHistory();
    const [checkBtnConfirm, setCheckBtnConfirm] = useState(false);
    const [successLoad, setSuccessLoad] = useState(false);
    const nowDateDay = moment().format('yyyy년 MM월 DD일');
    const [userData, setUserData] = useState({});
    const alerText = {text: "확인"};
    const [checkListObject, setCheckListObject] = useState({check1 : 3, check2 : 3, check3 : 3, check4 : 3, check5 : 3});
    const [checkGeyakAgree, setCheckGeyakAgree] = useState(false);    
    const screenRef = createRef(null);
    const [image, takeScreenShot] = useScreenshot();

    const checkRef = useRef();

    useEffect(() => {
        console.log(props.location.state);
        try{
            if(!props.location.state.userData)
            {
                alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
            }else{
                setSuccessLoad(true);
                setUserData(props.location.state.userData);
            }
        }catch(error){
            alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
        }
        console.log(userData);
    }, [])

    useEffect(() => {       
        if(image){
            console.log(image);
            sendScreenShotImage();
        }
    }, [image]);

    const btnConfirmClickHandler = e =>
    {
        if(checkGeyakAgree !== true){
            alert('알림', "계약내용 확인에 체크 하지하지 않으셨습니다.", [alerText]);
            //console.log(checkRef);
            //console.log(checkRef.current.offsetTop);
            //window.scrollTo({top:checkRef.current.offsetTop, behavior:'smooth'});
            //checkRef.current.focus();
            return;
        }

        if(checkListObject.check1 !== 0){
            alert('알림', "1. 자필서명이행에 대해 재확인 하시길 바랍니다.", [alerText]);
            return;
        }
        if(checkListObject.check2 !== 0){
            alert('알림', "2. 알릴의무 관련 유의사항 에 대해 재확인 하시길 바랍니다.", [alerText]);
            return;
        }
        if(checkListObject.check3 !== 0){
            alert('알림', "3. 계약서류 제공에 대해 재확인 하시길 바랍니다.", [alerText]);
            return;
        }
        if(checkListObject.check4 !== 0){
            alert('알림', "4. 설명의무 준수에 대해 재확인 하시길 바랍니다.", [alerText]);
            return;
        }
        if(checkListObject.check5 !== 0){
            alert('알림', "5. 취급자 사항 확인에 대해 재확인 하시길 바랍니다.", [alerText]);
            return;
        }

        console.log(checkListObject);
        alert("알림", '위 내용에 동의 하고 서명 하시겠습니까?',[
            {
                text : '아니요',
                onPress : () => {
                    setCheckBtnConfirm(false);
                },
            },
            {
                text : '예',
                onPress : () => {
                    sendAgreeDate(false);
                },
            }
        ]);
    }

    const sendAgreeDate = async() =>{
        let params = {
            url: '/Imo/MobileBigyo/SaveAgreeDate',
            pk : props.location.state.userData.userInfo.pk,
            gubun : 'perfect_sale_date',
            imochk: 'ok',
        };
        Toast.loading("Loding...");
        let result = await service.getPost(params);
        Toast.hide();
        if(result.data.status.ercode !== '0'){
            alert('알림', result.data.status.ermsg, [alerText]);
        }else{
            setCheckBtnConfirm(true);
        }
    }

    const nextButtonClickHandler = e =>{
        window.scrollTo(0, 0);
        takeScreenShot(screenRef.current);
    }
    const sendScreenShotImage = async() =>{
        if(image){
            let params = {
                url: '/Imo/MobileBigyo/SaveScreenshot',
                pk : props.location.state.userData.userInfo.pk,
                geyakja_nm : props.location.state.userData.userInfo.geyakja_nm,
                screen_data : image,
                imochk: 'ok'
            };
            Toast.loading("Loding...");
            let result = await service.getPost(params);
            Toast.hide();
            if(result.data.status.ercode !== '0'){
                alert('알림', result.data.status.ermsg, [alerText]);
            }else{
                history.replace("/financeFinish", {userData : props.location.state.userData});
            }
            
        }
    }
    const checkChange = (checkValues, val) =>{
        setCheckListObject(
            {
                ...checkListObject,
                [checkValues] : val
            }
        )
    }
    const checkGeyakAgreeChangeHandler = (e) =>{
        console.log(e.target.checked);
        setCheckGeyakAgree(e.target.checked);
    }

    return (
        <>  
        {
            successLoad?
            <div ref={screenRef}>
                <MainLayout mainTitle="완전판매확인서">
                    <div style={{textAlign : 'center', border: 'solid thin', backgroundColor : '#fffcf4'}}>
                        보험 가입을 진심으로 감사드립니다.
                    </div>
                    <WhiteSpace/>
                    <div style={{lineHeight : '1.8', border :'solid thin', padding: '5px'}}>
                        보험계약 체결 시 알릴의무 고지, 자필서명(계약자, 피보험자)이행, 상품에 대한 중요사항 설명은 계약의 체결 및 유지, 보험금 청구 시에 반드시
                        필요한 내용입니다. 아래 점검을 통해 다시 한 번 확인 하시길 바랍니다.
                    </div>
                    <WhiteSpace/>
                    <div style={{border: 'solid thin', backgroundColor : 'white'}}>
                        <Flex>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                계약자
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                {userData.userInfo.geyakja_nm}
                            </div>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                생년월일
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin'}}>
                                {userData.userInfo.birth_day}
                            </div>             
                        </Flex>
                        <Flex>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                피보험자
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                {userData.userInfo.pibohumja_nm}
                            </div>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                월보험료
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin'}}>
                                {userData.userInfo.youngsu_p}
                            </div>
                        </Flex>
                        <Flex>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험회사
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                {userData.sangpumListData[0].wonsusa_nm}
                            </div>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험종목
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin'}}>
                                {userData.userInfo.sangpum_category_nm}
                            </div>             
                        </Flex>
                        <Flex>
                        <div style={{width : '20%', textAlign : 'center', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험상품명
                            </div>
                            <div style={{width : '70%', textAlign : 'center'}}>
                                {userData.sangpumListData[0].sangpum_nm}
                            </div>
                        </Flex>
                    </div>
                    <WhiteSpace/>
                    <div>
                        <div>
                            <Checkbox ref={checkRef} onChange={checkGeyakAgreeChangeHandler}>계약내용을 확인하였음</Checkbox>
                        </div>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        다음 질문 사항에 예/아니오를 체크하여 주시기 바랍니다.
                    </div>                
                    <WhiteSpace/>
                    
                    <Card>
                        <Card.Header title="1. 자필서명이행"></Card.Header>
                        <Card.Body>
                            <div>
                                청약 서류에 계약자계서 직접 서명 하셨습니까?(전자적 방법 포함)
                            </div>
                        </Card.Body>
                        <Card.Footer content={<RadioYesNo checkChange={checkChange} checkValue="check1"/>}></Card.Footer>
                    </Card>
                    <WhiteSpace/>
                    <Card>
                        <Card.Header title="2. 알릴의무 관련 유의사항"></Card.Header>
                        <Card.Body>
                            <div>
                            계약전 알릴의무사항을 사실대로 고지하였으며, 이와 다를 경우 계약이 해지되거나 제한될 수 있음을 들으셨습니까?
                            </div>
                        </Card.Body>
                        <Card.Footer content={<RadioYesNo checkChange={checkChange} checkValue="check2"/>}></Card.Footer>
                    </Card>
                    <WhiteSpace/>
                    <Card>
                        <Card.Header title="3. 계약서류 제공"></Card.Header>
                        <Card.Body>
                            <div>
                            청약서, 약관, 상품설명서를 받으시고 전자적방법(e-mail 또는 모바일) 포함), 약관의 중요한 내용에 대해 설명을 들으셨습니까?
                            </div>
                        </Card.Body>
                        <Card.Footer content={<RadioYesNo checkChange={checkChange} checkValue="check3"/>}></Card.Footer>
                    </Card>
                    <WhiteSpace/>
                    <Card>
                        <Card.Header title="4. 설명의무 준수"></Card.Header>
                        <Card.Body>
                            <div>
                            (상품 주요 내용에 대한 설명) 가입하신 보험계약의 중요 내용인 보험금 지급사유, 보상한도, 보험기간, 보험료에 대한 설명<br/>
                            (환급금 안내) 중도 해지하거나 만기시 돌려받는 금액이 원금보다 적거나 없을 수 있다는 내용 * 상기 항목을 포함한 상품의 주요 내용에 대한 설명을 들으셨습니까?
                            </div>
                        </Card.Body>
                        <Card.Footer content={<RadioYesNo checkChange={checkChange} checkValue="check4"/>}></Card.Footer>
                    </Card>
                    <WhiteSpace/>
                    <Card>
                        <Card.Header title="5. 취급자 사항 확인"></Card.Header>
                        <Card.Body>
                            <div>
                            청약서 상에 기재된 모집자의 소속과 이름을 확인하였으며, 대리인이 아닌 모집자에게 직접 설명을 듣고 청약 하셨습니까?
                            </div>
                        </Card.Body>
                        <Card.Footer content={<RadioYesNo checkChange={checkChange} checkValue="check5"/>}></Card.Footer>
                    </Card>
                    <WhiteSpace/>
                    <div>
                    계약자 본인은 가입한 상품의 주요내용에 대해 모집인으로부터 충분한 설명을 듣고 이해하였습니다.
                    </div>
                    <WhiteSpace/>
                    <div style={{textAlign : 'center'}}>
                        {nowDateDay}
                    </div>
                    <WhiteSpace/>
                    <div>
                        <Flex style={{justifyContent: 'flex-end'}}>
                            <div style={{wordSpacing : '2rem'}}>
                                계약자 {userData.userInfo.geyakja_nm}
                            </div>
                            <div style={{paddingLeft : '3em'}}>
                                <Button type="primary" inline="true" size="small"
                                    style={{display : checkBtnConfirm?'none':''}} onClick={btnConfirmClickHandler}>
                                    확인
                                </Button>
                                <div style={{display : checkBtnConfirm?'':'none'}}>
                                    ({userData.userInfo.geyakja_nm})
                                </div>
                            </div>
                        </Flex>
                    </div>
                    <WhiteSpace/>
                    <div>
                        <Button type="primary" disabled={checkBtnConfirm?false:true } block="true" onClick={nextButtonClickHandler}>다음</Button>
                    </div>
                </MainLayout> 
            </div>
            :
            <div>잘못된 접근</div>
        }
        </>
    )
};