/* eslint-disable react-hooks/exhaustive-deps */
import React, {useState, useEffect, createRef} from 'react';
import { useHistory } from "react-router-dom";
import { MainLayout } from 'styleComponents/MainLayout';
import {Button, Accordion, Checkbox, Modal, Toast, WhiteSpace} from "antd-mobile";
import 'antd-mobile/dist/antd-mobile.css';
import * as service from 'service/post';
import { useScreenshot } from "use-react-screenshot";


const alert = Modal.alert;

export default function PrivacyAgree(props){
    const [checkAgree, setCheckAgree] = useState(false);
    const [nextBtnEnable, setNextBtnEnable] = useState(false);
    const [image, takeScreenShot] = useScreenshot();
    const screenRef = createRef(null);
    const history = useHistory();
    const alerText = {text: "확인"};
    
    useEffect(() => {
        //console.log(props.location.state);
        try{
            if(!props.location.state.userData)
            {
                alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
            }else{
                setNextBtnEnable(true);
            }
        }catch(error){
            alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
        }
        
    }, [])

    useEffect(() => {       
        if(image){
            console.log(image);
            sendScreenShotImage();
        }
    }, [image]);

    const nextButtonClickHandler = e =>{
        console.log(props.location.state);
        if(!checkAgree){
            alert('알림',"개인정보 처리 동의를 하지 않으셨습니다.",[alerText]);
            return;
        }
        window.scrollTo(0, 0);
        takeScreenShot(screenRef.current);
    }
    const sendScreenShotImage = () =>{
        if(image){
            sendAgreeDate();
        }
    }

    const sendAgreeDate = async() =>{
        let params = {
            url: '/Imo/MobileBigyo/SaveAgreeDate',
            pk : props.location.state.userData.userInfo.pk,
            gubun : 'privacy',
            imochk: 'ok',
        };
        Toast.loading("Loding...");
        let result = await service.getPost(params);
        Toast.hide();
        if(result.data.status.ercode !== '0'){
            alert('알림', result.data.status.ermsg, [alerText]);
        }else{
            sendScreenImage();
        }
    }

    
    const sendScreenImage = async() =>{
        if(image){
            let params = {
                url: '/Imo/MobileBigyo/SaveScreenshot',
                pk : props.location.state.userData.userInfo.pk,
                geyakja_nm : props.location.state.userData.userInfo.geyakja_nm,
                screen_data : image,
                imochk: 'ok'
            };
            Toast.loading("Loding...");
            let result = await service.getPost(params);
            Toast.hide();
            if(result.data.status.ercode !== '0'){
                alert('알림', result.data.status.ermsg, [alerText]);
            }else{
                history.replace("/financeNoti", {userData : props.location.state.userData});
            }            
        }
    }
    return (
        <>
            <div ref={screenRef}>
                <MainLayout mainTitle="계약의 체결 이행 등을 위한 개인(신용)정보 처리 동의서">
                    <div style={{width: "100%"}}>
                        <div style={{lineHeight : '1.8'}}>
                        당사 및 당사 업무수탁자는 개인정보보호법 및 신용정보의 이용 및 보호에 관한 법률에 따라 보험 계약 체결과 관련하여 귀하의 개인(신용)정보를 다음과 같이 수집 이용하고자 합니다.
                        본 동의는 보험계약 체결 이행 유지 관리에 필수적인 동의로 비동의시 관련 업무 수행이 불가능합니다.
                        본 동의서에 의한 개인(신용)정보 조회는 귀하의 신용등급에 영향을 주지 않습니다.
                        </div>
                        <WhiteSpace/>
                        <Accordion>
                            <Accordion.Panel header="개인(신용)정보 수진 이용 목적">
                                보험계약의 체결 유지 관리 (부환, 갱신 포함) 및 금융거래관련업무 처리
                                <WhiteSpace/>
                                민원처리, 사고조사(보험사기 포함) 및 분쟁 대응
                                <WhiteSpace/>
                                보험업법, 금융소비자보호법 등 법령에 따른 보험모집질서유지
                                <WhiteSpace/>
                                기 계약자에 대한 보험계약 상담
                            </Accordion.Panel>
                            <Accordion.Panel header="개인(신용)정보 수집 이용 항목">
                                개인식별정보 : 성명, 주소, 생년월일, 전자우편주소, 유무선 전화번호, 성별, 국적, 직업, 운전여부, 신용관리번호, 국내거소신고번호, 음성정보
                                <WhiteSpace/>
                                고유식별정보 : 주민등록번호, 외국인등록번호, 여권번호(법령 상 근거가 있는 경우에 한함)
                                <WhiteSpace/>
                                신용정보 : 금융거래업무 관련 정보, 보험계약정보(보험사, 증권번호, 종목, 상품군, 상품명, 계약자, 피보험자, 보험료, 보험시작일 등), 계약 전 알릴 의무사항 관련 정보(직업 등)

                            </Accordion.Panel>
                            <Accordion.Panel header="개인(신용)정보 보유 이용 기간">
                                보험계약 미가입자 : 수집 이용 동의일로부터 3개월
                                <WhiteSpace/>
                                보험계약 가입자 : 수집 이용 동의일로부터 거래 종료 후 5년까지 (거래종료일은 보험계약 만기, 해지, 취소, 철회일 또는 
                                소멸일 및 채권, 채무 소멸일 중 가장 나중에 도래한 사유를 기준으로 판단. 단 5년 경과후에도 수집 이용 목적 달성을 위해 필요한 경우 별도 보관)
                            </Accordion.Panel>
                        </Accordion>
                        <WhiteSpace/>
                        위와 같이 개인(신용)정보를 처리하는데 동의합니다.<br/><br/>
                        <div style={{textAlign : "right"}}> 
                            <Checkbox key={1} onChange={e => setCheckAgree(e.target.checked)}>
                                &nbsp;동의함
                            </Checkbox>
                        </div>
                    </div>
                    <WhiteSpace/>
                    <Button type="primary" disabled={nextBtnEnable && checkAgree? false : true} block="true" onClick={nextButtonClickHandler}>다음</Button>
                </MainLayout>
            </div>
        </>
    )
};