import React from 'react';
import {NavBar} from "antd-mobile";
import 'antd-mobile/dist/antd-mobile.css';
import logo from 'img/incar_logo.png'
export default function NavHeader(props){

    return (
        <>
            <NavBar>
            <div style={{textAlign:'center'}}><img style={{maxHeight : '100%', maxWidth : '65%'}} src={logo} alt="인카로고"></img></div>
            </NavBar>
        </>
    )
};