/* eslint-disable react-hooks/exhaustive-deps */
/**비교견적확인서 */
import React, {useState, useEffect, createRef} from 'react';
import { useHistory } from "react-router-dom";
import {Button, Flex, Modal, WhiteSpace, Tag, Toast} from "antd-mobile";
import { MainLayout } from 'styleComponents/MainLayout';
import 'antd-mobile/dist/antd-mobile.css';
import moment from "moment";
import * as service from 'service/post';
import { useScreenshot } from "use-react-screenshot";
import {ReactComponent as TouchIcon} from 'svg/touch.svg';

const alert = Modal.alert;
const fledDivStyle3 = {
    display : 'flex',
    justifyContent: 'center',
    alignItems: 'center'
}

export default function BohumReport(props){
    const history = useHistory();
    const [checkBtnConfirm, setCheckBtnConfirm] = useState(false);
    const [successLoad, setSuccessLoad] = useState(false);
    const nowDateDay = moment().format('yyyy년 MM월 DD일');
    const [userData, setUserData] = useState({});
    const [userConfirm, setUserConfirm] = useState(false);
    const screenRef = createRef(null);
    const [image, takeScreenShot] = useScreenshot();
    const alerText = {text: "확인"};

    useEffect(() => {
        console.log(props.location.state);
        try{
            if(!props.location.state.userData)
            {
                alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
            }else{
                setSuccessLoad(true);
                setUserData(props.location.state.userData);
            }
        }catch(error){
            alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
        }
        console.log(userData);
    }, []);

    useEffect(() => {       
        if(image){
            console.log(image);
            sendScreenShotImage();
        }
    }, [image]);

    const btnConfirmClickHandler = e =>
    {
        alert("알림", '위 내용에 동의 하고 서명 하시겠습니까?',[
            {
                text : '아니요',
                onPress : () => {
                    setCheckBtnConfirm(false);
                },
            },
            {
                text : '예',
                onPress : () => {
                    sendAgreeDate(false);
                },
            }
        ]);
    }

    const sendAgreeDate = async() =>{
        let params = {
            url: '/Imo/MobileBigyo/SaveAgreeDate',
            pk : props.location.state.userData.userInfo.pk,
            gubun : 'comp_agree_date',
            imochk: 'ok',
        };
        Toast.loading("Loding...");
        let result = await service.getPost(params);
        Toast.hide();
        if(result.data.status.ercode !== '0'){
            alert('알림', result.data.status.ermsg, [alerText]);
        }else{
            setCheckBtnConfirm(true);
        }
    }
    const nextButtonClickHandler = e =>{
        window.scrollTo(0, 0);
        takeScreenShot(screenRef.current);
    }
    const sendScreenShotImage = async() =>{
        if(image){
            let params = {
                url: '/Imo/MobileBigyo/SaveScreenshot',
                pk : props.location.state.userData.userInfo.pk,
                geyakja_nm : props.location.state.userData.userInfo.geyakja_nm,
                screen_data : image,
                imochk: 'ok'
            };
            Toast.loading("Loding...");
            let result = await service.getPost(params);
            Toast.hide();
            if(result.data.status.ercode !== '0'){
                alert('알림', result.data.status.ermsg, [alerText]);
            }else{
                history.replace("/perfectSale", {userData : props.location.state.userData});
            }
            
        }
    }

    const userConfirmOnChangeHandler = selected => {
        if(!selected){
            selected = true;
        }
        setUserConfirm(selected);
    }
    return (
        <>  
        {
            successLoad?
            <div ref={screenRef}>
                <MainLayout mainTitle="보험상품 비교·설명 확인서">
                    <div>
                        1. 보험가입 내용
                    </div>
                    <WhiteSpace/>
                    <div style={{border: 'solid thin'}}>
                        <Flex>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                계약자
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                {userData.userInfo.geyakja_nm}
                            </div>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                생년월일
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin'}}>
                                {userData.userInfo.birth_day}
                            </div>             
                        </Flex>
                        <Flex>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험회사
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                {userData.sangpumListData[0].wonsusa_nm}
                            </div>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험종목
                            </div>
                            <div style={{...fledDivStyle3, width : '30%', textAlign : 'center', borderBottom : 'solid thin'}}>
                                {userData.userInfo.sangpum_category_nm}
                            </div>             
                        </Flex>
                        <Flex style={{alignItems : 'stretch'}}>
                        <div style={{width : '20%', textAlign : 'center', fontSize: '0.8em', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험상품명
                            </div>
                            <div style={{width : '80%', textAlign : 'center'}}>
                                {userData.sangpumListData[0].sangpum_nm}
                            </div>
                        </Flex>
                    </div>
                    <WhiteSpace/>
                    <div>
                        2. 비교설명들은 보험회사 및 보험상품(3개이상의 동종, 유사 상품 목록)
                    </div>                
                    <WhiteSpace/>
                    <div style={{border: 'solid thin'}}>
                        <Flex>
                            <div style={{...fledDivStyle3, width : '10%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                구분
                            </div>
                            <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험회사
                            </div>
                            <div style={{...fledDivStyle3, width : '70%', textAlign : 'center',  borderBottom : 'solid thin', backgroundColor : 'aliceblue'}}>
                                보험상품명
                            </div>
                        </Flex>
                        {
                            userData.sangpumListData.map(val => {
                                return (
                                    <Flex style={{alignItems : 'stretch'}}>
                                        <div style={{...fledDivStyle3, width : '10%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                            {val.no}
                                        </div>
                                        <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                            {val.wonsusa_nm}
                                        </div>
                                        <div style={{...fledDivStyle3, width : '70%', textAlign : 'center',  borderBottom : 'solid thin'}}>
                                            {val.sangpum_nm}
                                        </div>
                                    </Flex>
                                );
                            })
                        }
                    </div>
                    <WhiteSpace/>
                    <div>
                        3. 보험상품 비교설명 관련 사항
                    </div>
                    <div style={{fontSize : '0.85em'}}>
                        아래 사항 확인 후 괄호로 된 부분을 <mark style={{backgroundColor : 'red', color:'white'}}>터치</mark>하여 주세요 [<TouchIcon width="24px" height="24px"></TouchIcon>]
                    </div>
                    <WhiteSpace/>
                    <div style={{border: 'solid thin', padding : '5px'}}>
                    본인은 상기 보험상품에 청약하면서 3가지 이상의 동종 또는 유사 상품에 대하여 인카금융서비스 대리점 소속 모집종사자로부터 ①보험금 및 지급사유, ②보험기간
                    ③보험료 ④보험금을 지급하지 아니하는 사유, ⑤해지환급금에 관한 사항 ⑥재계약에 관한 사항, ⑦해당 보험상품의 차별화된 특징에 대하여 비교·설명을
                    ({<Tag onChange={userConfirmOnChangeHandler} disabled={userConfirm} style={{color : 'red'}}>들었습니다</Tag>})
                    </div>
                    <WhiteSpace/>
                    <div style={{textAlign : 'center'}}>
                        {nowDateDay}
                    </div>
                    <WhiteSpace/>
                    <div>
                        <Flex style={{justifyContent: 'flex-end'}}>
                            <div style={{wordSpacing : '2rem'}}>
                                계약자 {userData.userInfo.geyakja_nm}
                            </div>
                            <div style={{paddingLeft : '3em'}}>
                                <Button type="primary" inline="true" size="small"
                                    disabled={!userConfirm?"true":""}
                                    style={{display : checkBtnConfirm?'none':''}} onClick={btnConfirmClickHandler}>
                                    확인
                                </Button>
                                <div style={{display : checkBtnConfirm?'':'none'}}>
                                    ({userData.userInfo.geyakja_nm})
                                </div>
                            </div>
                        </Flex>
                    </div>
                    <WhiteSpace/>
                    <div style={{border: 'solid thin'}}>
                        <Flex style={{alignItems : 'stretch'}}>
                            <div style={{...fledDivStyle3, width : '25%', fontSize : '1em', textAlign : 'center',  borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                확인자소속
                            </div>
                            <div style={{...fledDivStyle3, width : '40%', fontSize : '0.9em', textAlign : 'center',  borderRight : 'solid thin'}}>
                                인카금융서비스(주)
                            </div>
                            <div style={{...fledDivStyle3, width : '20%', fontSize : '1em', textAlign : 'center',  borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                설계사
                            </div>
                            <div style={{...fledDivStyle3, width : '25%', fontSize : '1em', textAlign : 'center'}}>
                                {userData.userInfo.sawon_nm}
                            </div>             
                        </Flex>
                    </div>
                    <WhiteSpace/>
                    <div>
                        <Button type="primary" disabled={checkBtnConfirm?false:true } block="true" onClick={nextButtonClickHandler}>다음</Button>
                    </div>
                </MainLayout>
            </div>
            :
            <div>잘못된 접근</div>
        }
        </>
    )
};