/**금융소비자보호를 위한 계약체결 고지의무 확인서 */
import React, {useState} from 'react';
import { useHistory } from "react-router-dom";
import {Button, Space, Descriptions, Row, Col, Checkbox, Modal, Card} from "antd";
import 'antd/dist/antd.css';
import * as service from 'service/post';
import { recDataToArray } from 'service/library';

const gridColCenterStyle = {
    textAlign : "center"
}

const gridColLeftStyle = {
    textAlign : "left",
}

const gridColUserInfoStyle = {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    wordBreak : 'break-all'
}

const colSpanSet = {
    left : 3,
    center : 16,
    right : 5
}

export default function PerfectSaleVariable(props){
    const history = useHistory();
    const checkLabel = [{label:"예", value:"y"},{label:"아니오", value:"n"}];
    const nextButtonClickHandler = e =>{
        let checkAllYesCount = document.querySelectorAll('input[type=checkbox][value=y]');
        let checkAllYes =   document.querySelectorAll('input[type=checkbox][value=y]:checked');
        let checkAllNo  =   document.querySelectorAll('input[type=checkbox][value=n]:checked');

        if(checkAllNo.length > 0 ){
            Modal.info({content : "아니오를 선택한 항목이 있습니다."});
            return;
        }
        
        if(checkAllYes.length !==checkAllYesCount.length){
            Modal.info({content : "전체 항목에 동의 하지 않아서 다음으로 이동 할 수 없습니다."});
            return;
        }

        //console.log(checkall.length);

//        console.log(checkall);    
        //history.push("/insuranceComp");
    }
    return (
        <>
            <div style={{
                        display : 'flex',
                        justifyContent : 'center'
                        }}>
                <div style={{
                            borderStyle : 'solid',
                            display : 'inline-block',
                            padding : '0 50px'
                            }}>
                    완전판매확인서(회사보관용)
                </div>
            </div>
            
            <br/>
            <div style={{textAlign : 'center'}}>보험 가입을 진심으로 감사 드립니다.</div>
            <br/>
            <div>
                ◎ 보험계약 체결 시 알릴의무 고지, 자필서명(계약자, 피보험자) 이행, 상품에 대한 중요사항 
                설명은 계약의 체결 및 유지, 보험금 청구 시에 반드시 필요한 내용입니다.
                아래의 점검을 통해 다시 한 번 확인하시길 바랍니다.
                <br/>
                <br/>                
                <div style={{borderStyle :'solid'}}>
                    <Row style={{borderBottomStyle : 'ridge'}}>
                        <Col style={gridColUserInfoStyle} span={4}>계약자</Col>
                        <Col style={gridColUserInfoStyle} span={4}>홍길동</Col>
                        <Col style={gridColUserInfoStyle} span={4}>생년월일</Col>
                        <Col style={gridColUserInfoStyle} span={4}>19850404</Col>
                        <Col style={gridColUserInfoStyle} span={4}>피보험자</Col>
                        <Col style={gridColUserInfoStyle} span={4}>홍길동</Col>
                    </Row>
                    <Row style={{borderBottomStyle : 'ridge'}}>
                        <Col style={gridColUserInfoStyle} span={4}>월보험료</Col>
                        <Col style={gridColUserInfoStyle} span={4}>1,000,000</Col>
                        <Col style={gridColUserInfoStyle} span={4}>증권번호</Col>
                        <Col style={gridColUserInfoStyle} span={4}>J290293HJJ529259</Col>
                        <Col style={gridColUserInfoStyle} span={4}>보험회사</Col>
                        <Col style={gridColUserInfoStyle} span={4}>삼성화재</Col>
                    </Row>
                    <Row>
                        <Col style={gridColUserInfoStyle} span={4}>보험종목</Col>
                        <Col style={gridColUserInfoStyle} span={4}>자동차</Col>
                        <Col style={gridColUserInfoStyle} span={4}>상품군</Col>
                        <Col style={gridColUserInfoStyle} span={4}>개인용</Col>
                        <Col style={gridColUserInfoStyle} span={4}>보험상품</Col>
                        <Col style={gridColUserInfoStyle} span={4}>개인용상품</Col>
                    </Row>
                </div>
                <br/>
                <Space direction="vertical">
                    <div>
                        <label>1. 계약내용 확인</label>
                        <div>고객님께서 가입하신 보험상품이 위의 계약자명/ 보험회사/ 보험 상품명이 맞으십니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>2. 자필서명 이행 </label>
                        <div>청약서류에 계약자께서 직접 서명 하셨습니까? (전자적 방법 포함)</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>3. 알릴의무 관련 유의사항 </label>
                        <div>계약전 알릴의무사항을 사실대로 고지하였으며, 이와 다를 경우 계약이 해지되거나 제한될 수 있음을 들으셨습니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>4. 계약서류 제공</label>
                        <div>청약서, 약관, 상품설명서를 받으시고 (전자적방법(E-MAIL or 모바일) 포함), 약관의 중요한 내용에 대해 설명을 들으셨습니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>5. 상품 주요 내용 안내  </label>
                        <div>가입하신 보험계약의 중요 내용인 보험금 지급사유,보상한도,보험기간,보험료에 대한 설명을 들으셨습니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>6. 환급금 안내</label>
                        <div>중도 해지하거나 만기시 돌려받는 금액이 원금보다 적거나 없을수 있다는 내용을 들으셨습니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>7. 취급자 사항 확인</label>
                        <div>청약서 상에 기재된 모집자의 소속과 이름을 확인 하였으며, 대리인이 아닌 모집자에게 직접 설명을 듣고 청약 하셨습니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>8. 적합성 진단 이행여부 확인</label>
                        <div>적합성 진단 설문지 내용을 정확히 확인하고 본인이 직접 선택 작성하신게 맞으십니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>9. 실적배당형 상품의 안내</label>
                        <div>상기 상품은 특별계정(펀드) 운용실적에 따라 투자손익이 계약자 또는 수익자에게 귀속됩니다. 실적 배당형 상품으 특징에 대해 설명 들으셨습니까?</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>10. 펀드선택시 유의사항 안내</label>
                        <div>계약자가 선택하신 펀드에 따라 투자 리스크와 수익률, 운용보수에서 차이가 발생할 수 있습니다.
                        해외펀드의 경우, 환율변동에 따른 환위험에 노출될 수 있습니다 펀드 선택 시 유의사항에 대한 설명을 들으셨습니까?
                        </div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                    <div>
                        <label>11. 원금손실 가능성 안내</label>
                        <div>원금손실 및 해지환급금의 변동 가능성에 대하여 들으셨습니까? (최저보증이율이 없는 상품에 가입했을 때, 경우에 따라서 원금손실이 발생할 수 있습니다.)</div>
                        <div><Checkbox.Group options={checkLabel}></Checkbox.Group></div>
                    </div>
                </Space>
                <br/>
                <br/>
                <label>※ 계약자 본인은 가입한 상품의 주요내용에 대해 모집인으로부터 충분한 설명을 듣고 이해 하였습니다. </label>
                <br/>
                <br/>
                <Row justify="end">
                    <Col span="4"><center>날짜</center></Col>
                    <Col span="6"><center>2021-06-03</center></Col>
                </Row>
                <Row justify="end">
                    <Col span="4"><center>서명</center></Col>
                    <Col span="6"><center>홍길동</center></Col>
                </Row>
                
            </div>
            <br/>
            <br/>
            <Button type="primary" block onClick={nextButtonClickHandler}>다음</Button>
        </>
    )
};