import React from 'react';
import {Spin } from "antd";
import 'antd/dist/antd.css';

const spinStyle = {
    position : "fixed",
    transform : "translate(-50%, -50%)",
    top : "50%",
    left : "50%"
}

export function LsySpin(props){
    return (
        <Spin style={spinStyle} spinning={props.spinning}>
            {props.children}
        </Spin>
    );
}