import {Route} from "react-router-dom";
import Home from "components/Home";
import InsuranceComp from "components/InsuranceComp";
import PrivacyAgree from "components/PrivacyAgree";
import FinanceNoti from "components/FinanceNoti";
import PerfectSale from "components/PerfectSale";
import PerfectSaleVariable from "components/PerfectSaleVariable";
import BohumReport from "components/BohumReport";
import GetDataList from "components/GetDataList";
import FinanceFinish from "components/FinanceFinish";
import './App.css';

export default function App() {
  return (
    <>
      <Route path="/home" component={Home}/>
      <Route path="/insuranceComp" component={InsuranceComp}/>
      <Route path="/financeNoti" component={FinanceNoti}/>
      <Route path="/privacyAgree" component={PrivacyAgree}/>
      <Route path="/perfectSale" component={PerfectSale}/>
      <Route path="/perfectSaleVariable" component={PerfectSaleVariable}/>
      <Route path="/bohumReport" component={BohumReport}/>
      <Route path="/customerConnect/:pk" component={GetDataList}/>
      <Route path="/financeFinish" component={FinanceFinish}/>
    </>
  );
};