/**금융소비자보호를 위한 계약체결 고지의무 확인서 */
import React, {useEffect, useState} from 'react';
import { useHistory } from "react-router-dom";
import {InputItem, Modal, Button, Toast,Card} from "antd-mobile";
import 'antd-mobile/dist/antd-mobile.css';
import * as service from 'service/post';

const alert = Modal.alert;

export default function GetDataList({match}){
    const history = useHistory();
    const [birthDay, setBirthDay] = useState();
    const alerText = {text: "확인"};
    const mountedComponents = () =>{
        if(match.params.pk === '')
        {
            alert('경고', "잘못된 경로로 접근 하였습니다.");
            Modal.info({content : "잘못된 경로로 접근 하였습니다."});
            return ;
        }
    }
    useEffect(mountedComponents,[match.params]);

    const onConfirmBtnClickHandler = e => {
        sendCustomerData();
    }

    const onBirthDayChangeHandler = e => 
    {
        setBirthDay(e);
    }

    const sendCustomerData = async()=>{
        let params = {
            url: '/Imo/MobileBigyo/GetAgreeInfo',
            pk : match.params.pk,
            birth_day : birthDay,
            imochk: 'ok',
        };
        Toast.loading("Loding...");
        let result = await service.getPost(params);
        Toast.hide();
        if(result.data.status.ercode !== '0'){
            alert('경고', result.data.status.ermsg,[alerText]);
        }else{            
            let resultData = {
                    userData : {
                        userInfo : result.data.object.info, 
                        sangpumListData : result.data.object.sangpum_rec
                    }
                };
           history.replace('/privacyAgree', resultData);
        }
    }
    
    const a = {
        position: "fixed",
        width: "100%",
        top: "30%"
    };
    
    const b = {
        display : 'block',
        textAlign : 'left',
        width : '100%',
        fontSize : "1.3em",
        padding : "5px",
        fontWeight : "bold"
    }
   
    return (
        <>
            <div style={a}>
                <div>
                    <img src="http://www.incar.co.kr/images/logo.gif" alt="로고"/>
                </div>
                <Card style={{background:"#e3f2fd",marginTop : "510x"}}>
                    <div style={b}>생년월일 입력 하세요</div>
                        <InputItem 
                                placeholder="예) 951021"
                                maxLength="6"
                                value={birthDay}
                                onChange={onBirthDayChangeHandler}
                                inputMode="decimal"
                                />
                        <Button style={{background:"#e3f2fd" , color :"black", fontWeight : "bold"}} inline="true" onClick={onConfirmBtnClickHandler}>확인</Button>
                </Card>
            </div>
        </>
    )
};