import React from 'react';
import NavHeader from 'styleComponents/NavHeader';
import {WhiteSpace } from "antd-mobile";
import 'antd/dist/antd.css';


export function MainLayout(props){
    return (
        <>
            <NavHeader/>
            <WhiteSpace/>
            <WhiteSpace/>
            <div style={{padding:'15px', margin:'0 10px', backgroundColor:'white', border:'solid thin #ddd'}}>
                <div>
                    <h3 style={{textAlign:'center', fontWeight : 'bold'}}>{props.mainTitle}</h3>
                    <hr style={{border : '0', backgroundColor : 'black', height : '2px'}}></hr>
                </div>
                {props.children}
            </div>
        </>
    );
}