/* eslint-disable react-hooks/exhaustive-deps */
/**금융소비자보호를 위한 계약체결 고지의무 확인서 */
import React, {useState, useEffect, createRef} from 'react';
import { useHistory } from "react-router-dom";
import {Button, Flex, Modal, WhiteSpace, Toast, Tag} from "antd-mobile";
import { MainLayout } from 'styleComponents/MainLayout';
import 'antd-mobile/dist/antd-mobile.css';
import moment from "moment";
import * as service from 'service/post';
import { useScreenshot } from "use-react-screenshot";
import {ReactComponent as TouchIcon} from 'svg/touch.svg';

const alert = Modal.alert;

const DivCol1 = {
    width : '10%',
    textAlign : 'center',
}

const DivCol2 = {
    width : '90%',
    textAlign : 'left',
    borderLeftStyle: 'solid',
    borderLeftWidth: 'thin',
    padding : '5px 2px'
}

const FlexDivStyle1 = {
    borderBottomStyle: 'solid',
    borderBottomWidth: 'thin',
    backgroundColor : 'rgb(234 234 234)'
}

const FlexDivStyle2 = {
    borderBottomStyle: 'solid',
    borderBottomWidth: 'thin'
}

const fledDivStyle3 = {
    display : 'flex',
    height :'30px',
    justifyContent: 'center',
    alignItems: 'center'
}

export default function FinanceNoti(props){
    const history = useHistory();
    const [checkBtnConfirm, setCheckBtnConfirm] = useState(false);
    const nowDateDay = moment().format('yyyy-MM-DD');
    const [userConfirm, setUserConfirm] = useState(false);
    const screenRef = createRef(null);
    const [image, takeScreenShot] = useScreenshot();
    const [successLoad, setSuccessLoad] = useState(false);
    const [userData, setUserData] = useState({});
    const alerText = {text: "확인"};
    
    useEffect(() => {
        //console.log(props.location.state);
        try{
            if(!props.location.state.userData)
            {
                alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
            }else{
                setSuccessLoad(true);
                setUserData(props.location.state.userData);
            }
        }catch(error){
            alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
        }
        
    }, []);

    useEffect(() => {       
        if(image){
            console.log(image);
            sendScreenShotImage();
        }
    }, [image]);

    const btnConfirmClickHandler = e =>
    {
        alert("알림", '위 내용에 동의 하고 서명 하시겠습니까?',[
            {
                text : '아니요',
                onPress : () => {
                    setCheckBtnConfirm(false);
                },
            },
            {
                text : '예',
                onPress : () => {
                    sendAgreeDate(false);
                },
            }
        ]);
    }

    const sendAgreeDate = async() =>{
        let params = {
            url: '/Imo/MobileBigyo/SaveAgreeDate',
            pk : props.location.state.userData.userInfo.pk,
            gubun : 'finance_noti',
            imochk: 'ok',
        };
        Toast.loading("Loding...");
        let result = await service.getPost(params);
        Toast.hide();
        if(result.data.status.ercode !== '0'){
            alert('알림', result.data.status.ermsg, [alerText]);
        }else{
            setCheckBtnConfirm(true);
        }
    }

    const userConfirmOnChangeHandler = selected => {
        setUserConfirm(selected);
    }

    const nextButtonClickHandler = e =>{
        window.scrollTo(0, 0);
        takeScreenShot(screenRef.current);
    }
    const sendScreenShotImage = async() =>{
        if(image){
            let params = {
                url: '/Imo/MobileBigyo/SaveScreenshot',
                pk : props.location.state.userData.userInfo.pk,
                geyakja_nm : props.location.state.userData.userInfo.geyakja_nm,
                screen_data : image,
                imochk: 'ok'
            };
            Toast.loading("Loding...");
            let result = await service.getPost(params);
            Toast.hide();
            if(result.data.status.ercode !== '0'){
                alert('알림', result.data.status.ermsg, [alerText]);
            }else{
                history.replace("/bohumReport", {userData : props.location.state.userData});
            }
            
        }
    }
    return (
        <>
        {
            successLoad?
                <div ref={screenRef}>
                    <MainLayout mainTitle="금융소비자보호를 위한 계약체결 고지의무 확인서">
                        <div style={{lineHeight : '1.8', border :'solid thin'}}>
                            금융소비자보호법 제26조, 동법 시행령 제24조, 동법 감독규정 제23조에 따라 보험대리점을 통해 보험상품 계약을 체결하는 
                            소비자에게 해당 내용을 고지하여야 합니다. 동 확인서는 보험대리점의 고지의무와 관련하여 다툼이 
                            있을 경우 증빙으로 활용되오니, 아래 사항을 확인하신 후 확인버튼을 눌러주시기 바랍니다.		
                        </div>            					
                        <WhiteSpace/>
                        <div style={{lineHeight : '1.3', border :'solid thin'}}>
                            <Flex style={{backgroundColor : 'aliceblue'}}>
                                <div style={{...DivCol1}}>구분</div>
                                <div style={{...DivCol2, textAlign : 'center'}}>확인사항</div>
                            </Flex>
                            <Flex style={{...FlexDivStyle1}}>
                                <div style={DivCol1}>1</div>
                                <div style={DivCol2}>
                                        보험대리점이 대리하는 보험회사의 명칭 및 업무내용(위탁판매)을 들으셨습니까?
                                </div>
                            </Flex>
                            <Flex style={{...FlexDivStyle2}}>
                                <div style={DivCol1}>2</div>
                                <div style={DivCol2}>
                                    여러 보험회사의 상품을 취급하는 지 들으셨습니까?
                                </div>
                            </Flex>
                            <Flex style={{...FlexDivStyle1}}>
                            <div style={DivCol1}>3</div>
                            <div style={DivCol2}>
                                설계사가 금융소비자보호법상 준수사항 미 준수, 금지행위 위반 등 동법을 위반하여 고객에게 손해를 발생시킨 경우 설계사 및 보험회사 등은 손해배상할 책임이 있음을 들으였습니까?
                                </div>
                            </Flex>
                            <Flex style={{...FlexDivStyle2}}>
                            <div style={DivCol1}>4</div>
                            <div style={DivCol2}>
                                설계사는 고객이 보험사에 지급해야하는 금전을 받을 권한이 없으며, 고객으로부터 청약, 해지 등 계약에 관한 의사표시를 수령할 수 있는 권한이 없음을 들으셨습니까?
                                </div>
                            </Flex>
                            <Flex style={{...FlexDivStyle1}}>
                            <div style={DivCol1}>5</div>
                            <div style={DivCol2}>
                                소비자의 신용정보 및 개인정보는 보험회사가 보유, 관리한다는 사실 여부를 들으셨습니까?
                                </div>
                            </Flex>
                            <Flex style={{...FlexDivStyle2}}>
                            <div style={DivCol1}>6</div>
                            <div style={DivCol2}>
                                설계사의 이력, 불완전판매비율, 계약유지율, 법적처분여부를 이클린보험서비스(www.e-cleanins.or.kr)에서 확인할 수 있는 사실을 들으셨습니까?
                                </div>
                            </Flex>
                            <Flex style={{...FlexDivStyle1}}>
                            <div style={DivCol1}>7</div>
                            <div style={DivCol2}>
                                보험설계사의 등록증명서(증표)를 확인하셨습니까?
                                </div>
                            </Flex>
                            {
                                userData.userInfo.bojong_gb !== '1' && (
                                    <Flex style={{...FlexDivStyle2}}>
                                        <div style={DivCol1}>8</div>
                                        <div style={DivCol2}>
                                            보험협회가 비교·공시하는 자동차 보험의 보험료 비교·공시 조회 결과를 확인하셨습니까?
                                            </div>
                                    </Flex>
                                )
                            }
                            
                        </div>
                        <WhiteSpace/>
                        <div>
                            ■ 고지의무 청취 확인
                        </div>
                        <div style={{fontSize : '0.85em'}}>
                            고지의무 청취 후 괄호로 된 부분을 <mark style={{backgroundColor : 'red', color:'white'}}>터치</mark>하여 주세요 [<TouchIcon width="24px" height="24px"></TouchIcon>]
                        </div>
                        <WhiteSpace/>
                        <div style={{borderStyle : 'solid', borderWidth : 'thin', padding:'2px'}}>
                            위 내용에 대하여 보험설계사 (<mark>{userData.userInfo.sawon_nm}</mark>)에게 직접 고지받았으며, 계약자(<mark>{userData.userInfo.geyakja_nm}</mark>)은/는 상기 
                            확인서를 [{<Tag onChange={userConfirmOnChangeHandler} disabled={userConfirm} style={{color : 'red'}}>설명받았음</Tag>}]을 확인합니다.
                        </div>
                        <WhiteSpace/>
                        <div style={{border: 'solid thin'}}>
                            <Flex>
                                <div style={{...fledDivStyle3, width : '20%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                    확인일
                                </div>
                                <div style={{...fledDivStyle3, width : '25%', textAlign : 'center', borderBottom : 'solid thin', borderRight : 'solid thin'}}>
                                    {nowDateDay}
                                </div>
                                <div style={{...fledDivStyle3, width : '20%', textAlign : 'center',  borderBottom : 'solid thin', borderRight : 'solid thin', backgroundColor : 'aliceblue', fontSize : "0.5em"}}>
                                    보험계약자
                                </div>
                                <div style={{width : '35%', textAlign : 'center', borderBottom : 'solid thin'}}>
                                    <Flex>
                                        <div style={{width: '50%', textAlign : 'center'}}>
                                            {userData.userInfo.geyakja_nm}
                                        </div>
                                        <div style={{...fledDivStyle3, width: '50%', textAlign : 'center'}}>
                                            <Button type="primary" inline="true" size="small"
                                                disabled={!userConfirm?"true":""}
                                                style={{display : checkBtnConfirm?'none':''}} onClick={btnConfirmClickHandler}>
                                                확인
                                            </Button>
                                            <div style={{display : checkBtnConfirm?'':'none'}}>
                                                ({userData.userInfo.geyakja_nm})
                                            </div>
                                        </div>
                                    </Flex>
                                </div>                    
                            </Flex>
                            <Flex>
                            <div style={{width : '20%', textAlign : 'center', borderRight : 'solid thin', backgroundColor : 'aliceblue'}}>
                                    증권번호
                                </div>
                                <div style={{width : '70%', textAlign : 'center'}}>
                                    {userData.userInfo.jgno}
                                </div>
                            </Flex>
                        </div>
                        <WhiteSpace/>
                        <Button type="primary" disabled={checkBtnConfirm?false:true } block="true" onClick={nextButtonClickHandler}>다음</Button>    
                    </MainLayout>
                </div>
            : 
            <div>
                잘못된 접근
            </div>
        }
        </>
    )
};