import React, {useState,useEffect} from 'react';
import {Spin, Space, Button, Select, DatePicker, Input, Checkbox, Row, Col, Card ,Radio } from "antd";
import 'antd/dist/antd.css';
import locale from 'antd/lib/date-picker/locale/ko_KR';
import 'moment/locale/ko'

import moment from 'moment';
import * as service from 'service/post';
import {recDataToArray} from 'service/library';
import { LsySpin } from 'styleComponents/commonComp';

import logo from 'img/logo.png';

const registCd = "1611446";
const registPw = "125.129.124.51_20080823.2027_1466548992";

var selectBojong = "";
var selectWonsusaCd = "";
var selectSangpumGunCd = "";
var selectSangpumListCd = "";
const {Option} = Select;

function InsuranceComp (){
    const [wonsusaList, setWonsusaList] = useState([]);//보험사
    const [wonsusaList2, setWonsusaList2] = useState([]);//보험사2,보험사3
    const [sangpumGunList, setSangpumGunList] = useState([]);
    const [sangpumList, setSangpumList] = useState([]);

    const [won1, setWon1] = useState([]);
    const [sangpumGun, setSangpumGun] = useState([]);
    const [won2, setWon2] = useState([]);
    const [sangpum2, setSangpumList2] = useState([]);

    const [won3, setWon3] = useState([]);
    const [sangpum3, setSangpumList3] = useState([]);

    const [selectSangpum1, setSelectSangpum1] = useState();
    const [selectSangpum2, setSelectSangpum2] = useState();
    const [selectSangpum3, setSelectSangpum3] = useState();

    const [loading, setLoading] = useState(true);
    
    const [confirmCheck1, setConfirmCheck1] = useState(false);
    const [confirmCheck2, setConfirmCheck2] = useState(false);
    const [confirmCheck3, setConfirmCheck3] = useState(false);

    const [showSearchValue, setShowSearchValue]= useState(true);

    useEffect(() => {
        setLoading(false);
    },[]);

    const [inputs, setInputs] = useState({
        geyakja : ""
        ,birth_date : ""
        ,hp_no : ""
        ,pj_nm : ""
        ,bh_start : ""
        ,youngsu_p : ""
        ,jgno : ""
        ,sex_gb : ""
    });
    const [sangpumState, setSangpumState] = useState(false);//자동차 선택시 상품군 disabled boolean값
    
    const selectSearchFilterOption = (input, option) =>{
        console.log(input, option);
        return option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0;
    }
    
    const rowStyle = {
        marginBottom: "10px"
    };

    const colStyle = {
        display: "flex",
        placeContent: "center",
        alignContent: "center",
        alignItems: "flex-start",
        flexDirection: "column",
        flexWrap: "nowrap",
        justifyContent: "center"
    };
   
    // 구분 선택
    const selectGubunChangeHanlder = (v)=>{
        selectBojong = v;
        clearSelect('7');
        sendWonsusaList(v);
        setSangpumState(false);

        setConfirmCheck1(true);
        setConfirmCheck2(true);
        setConfirmCheck3(true); 
        if(selectBojong === '1'){
            setSangpumState(true);
            setConfirmCheck2(false);
            setConfirmCheck3(false);     
        }else{
            setConfirmCheck2(true);
            if(selectBojong === '2'){
                setConfirmCheck3(false);  
            }
        }
    }

    // 원수사 선택
    const selectWonsusaChangeHandler = (v) => {
        selectWonsusaCd = v;
        clearSelect('6');
        sendSangpumGun(v);
        setWon1(v);
    }

    //상품군 선택
    const selectSangpumGunChangeHandler = (v) => {
        selectSangpumGunCd = v;
        clearSelect('5');
        setSangpumGun(v);
        sendSangpumList(v);
    }

    // 보험상품 선택
    const selectSangpumListChangeHandler = (v) => {
        selectSangpumListCd = v;
        clearSelect('4');
        setSelectSangpum1(v);
        sendSangpumTwoWonsusaList(v);
    }

    // 보험사2 선택
    const selectWonsusaChangeHandler2 = (v) => {
        clearSelect('3');
        setWon2(v);
        sendSangpumTwoList2(v);
    }

    // 보험상품2 선택
    const selectSangpumChangeHandler2 = (v) => {
        clearSelect('2');
       setSelectSangpum2(v);
    }

    // 보험사3 선택
    const selectWonsusaChangeHandler3 = (v) => {
        setWon3(v);
        clearSelect('1');
        sendSangpumTwoList3(v);
    }
    
    // 보험상품3 선택
    const selectSangpumChangeHandler3 = (v) => {
        clearSelect('0');
        setSelectSangpum3(v);
    }

    // Input value값 처리
    const onChangeInputs = (e) => {
        if(e.target.name === 'birth_date'){
            var tmp = e.target.value;
            tmp = tmp.replace(/[^0-9]/g,'');   // 입력값이 숫자가 아니면 공백
            e.target.value = tmp;
        }

        if(e.target.name === 'youngsu_p'){
            var tmp = e.target.value;
            tmp = tmp.replace(/[^0-9]/g,'');   // 입력값이 숫자가 아니면 공백
            tmp = tmp.replace(/,/g,'');          // ,값 공백처리
            e.target.value = tmp.replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        }

        if(e.target.name === 'hp_no'){
            var tmp = e.target.value.replace(/[^0-9]/g, "").replace(/(^02|^0505|^1[0-9]{3}|^0[0-9]{2})([0-9]+)?([0-9]{4})$/,"$1-$2-$3").replace("--", "-");
            e.target.value = tmp;
        }
      
        const { value, name } = e.target; // 우선 e.target 에서 name 과 value 를 추출
        setInputs({
        ...inputs, // 기존의 input 객체를 복사한 뒤
        [name]: value // name 키를 가진 값을 value 로 설정
        });
        console.log(inputs);
    }

    // 보험시작일(DatePicker) value값 처리
    const onChangeDateInputs = (momentString,dateString) => {
        console.log(momentString);
        console.log(dateString);
        if(momentString && dateString){
            setInputs({
                ...inputs, // 기존의 input 객체를 복사한 뒤
                bh_start : dateString,
                momnet_bh_start : moment(dateString, 'YYYY-MM-DD') // name 키를 가진 값을 value 로 설정
            });
        }
        
    }

    const clearArr1 = [setSelectSangpum3,setWon3,setSelectSangpum2,setWon2,setSelectSangpum1,setSangpumGun,setWon1];
    const clearArr2 = [setSangpumList3,setWonsusaList2,setSangpumList2,setWonsusaList2,setSangpumList,setSangpumGunList,setWonsusaList];

    //select 초기화 처리
    const clearSelect = (v) => {
        if(v > 3){
            for(var i=0; i < v; i++){
                clearArr1[i]();
                clearArr2[i]([]);
                if(selectBojong==='1'){
                    sendWonsusaList(selectBojong);
                }
            }
        }
    }
    
    const informCheck = () => {
        
        if(inputs.geyakja===''){
            alert("계약자명 입력하세요.");
            return;
        }
        if(inputs.birth_date===''){
            alert("생년월일 입력하세요.");
            return;
        }

        if(inputs.birth_date.length!==6){
            alert("생년월일 6자리가 잘못 입력되었습니다.");
            return;
        }
        
        if(inputs.hp_no===''){
            alert("계약자 연락처 입력하세요.");
            return;
        }

        var regExp = /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/;
        var strTelNum = inputs.hp_no;

        if( !regExp.test(strTelNum)) {
            alert("입력된 계약자 연락처가 올바르지 않습니다.");
            return;
        }

        var regExp = /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/;
        var strTelNum = inputs.hp_no; 
        if( !regExp.test(strTelNum)) {
            alert("계약자 연락처가 잘못 입력되었습니다.");
        }

        if(inputs.bh_start===''){
            alert("보험시작일 입력하세요.");
            return;
        }
        if(inputs.pj_nm===''){
            alert("피보험자명 입력하세요.");
            return;
        }
        if(inputs.youngsu_p===''){
            alert("월보험료 입력하세요.");
            return;
        }
        if(selectBojong===''){
            alert("구분(손보, 생보, 일반, 자동차) 선택하세요.");
            return;
        }
        if(selectWonsusaCd===''){
            alert("보험회사 선택하세요.");
            return;
        }

        if(selectSangpumGunCd===''){
            alert("상품군 선택하세요.");
            return;
        }
        if(!won2){
            alert("보험회사 선택하세요.");
            return;
        }
        if(!won3){
            alert("보험회사 선택하세요.");
            return;
        }
        if(selectBojong !== '1'){
            if(selectSangpumListCd==='' || sangpum2==='' || sangpum3===''){
                alert("보험상품 선택하세요.");
                return;
            }
        }
        //sendSave();
    }

    const sendSave = async() =>{
        let params = {
            ...inputs,
            url: '/Imo/MobileBigyo/SaveSangpum',
            registCd : registCd,
            registPw : registPw,
            bojong_gb : selectBojong,
            sangpum_category_cd : selectSangpumGunCd,

            wonsusa_cd1 : won1,
            wonsusa_cd2 : won2,
            wonsusa_cd3 : won3,

            sangpum_code1 : selectSangpumListCd,
            sangpum_code2 : selectSangpum2,
            sangpum_code3 : selectSangpum3,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false);
        if(result.data.status.ercode !== '0'){
            console.log(result.data.status.ermsg);
        }
    }
    
    const sendWonsusaList = async (bojong) => {
        let params = {
            url: '/Etc/ICM/WonsusaName',
            registCd : registCd,
            registPw : registPw,
            bojong : bojong,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false);
        setWonsusaList([]);
        setWonsusaList2([]);
        if(result.data.status.ercode !== '0'){
            console.log(result.data.status.ermsg);
        }else{            
            let __wonsusaArr = recDataToArray(result.data.object.rec);
            if(__wonsusaArr && __wonsusaArr.length > 0 ){
                setWonsusaList(__wonsusaArr);
                if(bojong === '1'){
                    setWonsusaList2(__wonsusaArr);
                }
            }
        }
    }

    var sendSangpumGun = async (wonsusaCd) => {
        let params = {
            url: '/Etc/ICM/CategoryName',
            registCd : registCd,
            registPw : registPw,
            wonsusa_cd : wonsusaCd,
            bojong : selectBojong,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false);
        setSangpumGunList([]);
        if(result.data.status.ercode !== '0'){
            console.log(result.data.status.ermsg);
        }else{     
            let __tmpArray = recDataToArray(result.data.object.rec);
            if(__tmpArray && __tmpArray.length > 0 ){
                setSangpumGunList(__tmpArray);
            }
        }
    }

    const sendSangpumList = async (sangpumCategoryCd) => {
        let params = {
            url: '/Etc/ICM/ProductName',
            registCd : registCd,
            registPw : registPw,
            wonsusa_cd : selectWonsusaCd,
            bojong : selectBojong,
            sangpum_category_cd : sangpumCategoryCd,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false);
        setSangpumList([]);
        if(result.data.status.ercode !== '0'){
            console.log(result.data.status.ermsg);
        }else{           
            let __tmpArray = recDataToArray(result.data.object.rec);
            if(__tmpArray && __tmpArray.length > 0 ){
                setSangpumList(__tmpArray);
            }
        }
    }

    const sendSangpumTwoWonsusaList = async(sangpumCd) => {
        let params = {
            url: '/Etc/ICM/GetCompareSangpumTwo',
            registCd : registCd,
            registPw : registPw,
            wonsusa_cd : selectWonsusaCd,
            bojong : selectBojong,
            sangpum_category_cd : selectSangpumGunCd,
            sangpum_cd : sangpumCd,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false);
        let tmp = recDataToArray(result.data.object.rec);
        sendWonsusaList2(selectBojong,selectSangpumGunCd,tmp);
        
    }

    const sendWonsusaList2 = async (bojong,sangpumgun_cd,tmpArr) => {
        console.log(bojong,sangpumgun_cd,tmpArr);
        let params = {
            url: '/Etc/ICM/WonsusaName',
            registCd : registCd,
            registPw : registPw,
            bojong : bojong,
            sangpum_category_cd : sangpumgun_cd,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false);
        setWonsusaList2([]);
        if(result.data.status.ercode !== '0'){
            console.log(result.data.status.ermsg);
        }else{            
            let __wonsusaArr = recDataToArray(result.data.object.rec);
            console.log(__wonsusaArr);
            if(__wonsusaArr && __wonsusaArr.length > 0 ){
                setWonsusaList2(__wonsusaArr);
            }
        }
        if(tmpArr.length > 1){
            setShowSearchValue(false);
            for (var key in tmpArr) {
                if(key==='1'){
                    setWon2(tmpArr[key].wonsusa_code);
                    sendSangpumTwoList2(tmpArr[key].wonsusa_code);
                }else if(key==='2'){
                    setWon3(tmpArr[key].wonsusa_code);
                    sendSangpumTwoList3(tmpArr[key].wonsusa_code);
                }
            }
        }else{
            setShowSearchValue(true);
        }
    }

    const sendSangpumTwoList2 = async (wonsusaCd) => {
        let params = {
            url: '/Etc/ICM/ProductName',
            registCd : registCd,
            registPw : registPw,
            wonsusa_cd : wonsusaCd,
            bojong : selectBojong,
            sangpum_category_cd : selectSangpumGunCd,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false); 
        setSangpumList2([]);
        if(result.data.status.ercode !== '0'){
            console.log(result.data.status.ermsg);
        }else{           
            let __tmpArray = recDataToArray(result.data.object.rec);
            if(__tmpArray && __tmpArray.length > 0 ){
                setSelectSangpum2(__tmpArray[0].sangpum_cd);
                setSangpumList2(__tmpArray);
            }
        }
    }

    const sendSangpumTwoList3 = async (wonsusaCd) => {
        let params = {
            url: '/Etc/ICM/ProductName',
            registCd : registCd,
            registPw : registPw,
            wonsusa_cd : wonsusaCd,
            bojong : selectBojong,
            sangpum_category_cd : selectSangpumGunCd,
            imochk: 'ok',
        };
        setLoading(true);
        let result = await service.getPost(params);
        setLoading(false);
        setSangpumList3([]);
        if(result.data.status.ercode !== '0'){
            console.log(result.data.status.ermsg);
        }else{            
            let __tmpArray = recDataToArray(result.data.object.rec);
            if(__tmpArray && __tmpArray.length > 0 ){
                setSelectSangpum3(__tmpArray[0].sangpum_cd);
                setSangpumList3(__tmpArray);
            }
        }
    }
    
    return (
        <>
         <LsySpin spinning={loading}>
            <div style={{width : '100%'}}>
                <div style={{background : "#63a4ff"}}>
                    <div style={{width:'100%', height: "70px",textAlign:'center', fontSize : "20px", fontWeight : "bold", padding : "5px"}}><img style={{maxHeight : "100%", maxWidth : "100%"}} src={logo}/></div>
                    <div style={{width:'100%', textAlign:"left", color : "white",paddingLeft : "12px", fontSize : "1.2em", fontWeight : "bold"}}>보험 가입내용</div> 
                </div>
                
                <div style={{background:"#ececec", padding : "7px" , marginBottom : "5px"}}>
                
                    <Card size="small" title={<label style={{color : "red"}}><span style={{color : "black",fontWeight : "bold"}}>필수입력항목</span> *</label>}>
                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>계약자</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}><Input style={{width : "140px"}} placeholder="계약자명" onChange={onChangeInputs} name="geyakja" value={inputs.geyakja}/></Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>생년월일</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}>
                                <Space direction="horizontal">
                                    <Input maxLength="6" inputMode="decimal" style={{width : "140px"}} placeholder="주민번호 앞 6자리" onChange={onChangeInputs} name="birth_date" value={inputs.birth_date}/>
                                    
                                </Space>
                            </Col>
                        </Row>

                        <Row style={rowStyle} onChange={onChangeInputs}>
                            <Col style={colStyle} span={5}>성별</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Radio.Group name="sex_gb" defaultValue={1}>
                                <Radio value={1}>남</Radio>
                                <Radio value={2}>여</Radio>
                            </Radio.Group>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>연락처</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}><Input maxLength="13" placeholder="계약자 연락처 입력" inputMode="decimal" onChange={onChangeInputs} name="hp_no" value={inputs.hp_no}/></Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={{...colStyle,fontSize : "12px"}} span={5}>보험시작일</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}><DatePicker inputReadOnly={true} value={inputs.momnet_bh_start} name="bh_start" onChange={onChangeDateInputs} allowClear={false} locale={locale}/></Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={{...colStyle,fontSize : "12px"}} span={5}>피보험자명</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}><Input placeholder="피보험자명 입력" onChange={onChangeInputs} name="pj_nm" value={inputs.pj_nm}/></Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>월보험료</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}><Input inputMode="decimal" placeholder="월보험료 입력" onChange={onChangeInputs} name="youngsu_p" value={inputs.youngsu_p}/></Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={6}>증권번호</Col>
                            <Col span={18}><Input placeholder="증권번호 입력" onChange={onChangeInputs} name="jgno" value={inputs.jgno}/></Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>구분</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}>
                                <Select placeholder="구분" 
                                        style={{width:'100%'}}
                                        onChange={(value,option)=>selectGubunChangeHanlder(value,option)} 
                                        filterOption={selectSearchFilterOption}
                                        >
                                    <Option value="4">생명보험</Option> 
                                    <Option value="3">손해보험</Option>
                                    <Option value="2">일반보험</Option>
                                    <Option value="1">자동차</Option>
                                </Select>
                            </Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>보험회사</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}>
                                <Select placeholder="보험회사"
                                        style={{width:'100%'}} 
                                        onChange={selectWonsusaChangeHandler} 
                                        filterOption={selectSearchFilterOption}
                                        value = {won1}
                                        >
                                    {wonsusaList.map(item => (
                                        <Option key={item.wonsusa_cd} value={item.wonsusa_cd}>{item.wonsusa_nm}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>상품군</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}>
                                <Select placeholder="상품군"
                                        style={{width:'100%'}}
                                        onChange={selectSangpumGunChangeHandler} 
                                        filterOption={selectSearchFilterOption}
                                        value = {sangpumGun}
                                    >
                                    {sangpumGunList.map(item => (
                                        <Option key={item.sangpum_category_cd} value={item.sangpum_category_cd}>{item.name}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>

                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>보험상품</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}>
                                <Select placeholder="보험상품" showSearch
                                        style={{width:'100%'}}
                                        onChange = {selectSangpumListChangeHandler}
                                        filterOption = {selectSearchFilterOption}
                                        value = {selectSangpum1}
                                        disabled = {sangpumState}
                                    >
                                    {sangpumList.map(item => (
                                        <Option key={item.sangpum_cd} value={item.sangpum_cd}>{item.sangpum_nm}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>
                    </Card>
                </div>
                <div style={{background:"#ececec", padding : "7px" , marginBottom : "5px"}}>
                    <Card size="small">
                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>보험회사</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}>
                                <Select placeholder="보험회사" 
                                        style={{width:'100%'}}
                                        onChange = {selectWonsusaChangeHandler2}
                                        filterOption = {selectSearchFilterOption}
                                        value={won2}>
                                {wonsusaList2.map(item => (
                                        <Option key={item.wonsusa_cd} value={item.wonsusa_cd}>{item.wonsusa_nm}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>
                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>보험상품</Col> 
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>               
                            <Col span={18}>
                                <Select placeholder="보험상품" showSearch={showSearchValue}
                                        style={{width:'100%'}}
                                        name="selectSangpum2"
                                        value={selectSangpum2}
                                        onChange={selectSangpumChangeHandler2}
                                        filterOption = {selectSearchFilterOption}
                                        disabled = {sangpumState}>
                                {sangpum2.map(item => (
                                        <Option key={item.sangpum_cd} value={item.sangpum_cd}>{item.sangpum_nm}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>
                    </Card>
                </div>
                <div style={{background:"#ececec", padding : "7px", marginBottom : "5px"}}>
                    <Card size="small">
                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>보험회사</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col>
                            <Col span={18}>
                                <Select placeholder="보험회사" 
                                        style={{width:'100%'}}
                                        onChange = {selectWonsusaChangeHandler3}
                                        filterOption = {selectSearchFilterOption}
                                        value={won3}>
                                {wonsusaList2.map(item => (
                                        <Option key={item.wonsusa_cd} value={item.wonsusa_cd}>{item.wonsusa_nm}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>
                        <Row style={rowStyle}>
                            <Col style={colStyle} span={5}>보험상품</Col>
                            <Col style={{...colStyle,color : "red"}} span={1}>*</Col> 
                            <Col span={18}>
                                <Select placeholder="보험상품" showSearch={showSearchValue}
                                        style={{width:'100%'}}
                                        name="selectSangpum3"
                                        value={selectSangpum3}
                                        onChange={selectSangpumChangeHandler3}
                                        filterOption = {selectSearchFilterOption}
                                        disabled = {sangpumState}>
                                {sangpum3.map(item => (
                                        <Option key={item.sangpum_cd} value={item.sangpum_cd}>{item.sangpum_nm}</Option>
                                    ))}
                                </Select>
                            </Col>
                        </Row>
                    </Card>
                </div>
                <div style={{background:"#ececec", padding : "7px", marginBottom : "5px"}}>
                    <Card style={{background:"#e3f2fd"}}size="small" title="▶ 고객에게 발송되는 필수확인서">
                        <Row>
                            <Checkbox checked={confirmCheck1} inputReadOnly={true}>FA고지의무 확인서</Checkbox>
                        </Row>
                        <Row>
                            <Checkbox checked={confirmCheck2} inputReadOnly={true}>보험상품 비교,설명 확인서</Checkbox>
                        </Row>
                        <Row>
                            <Checkbox checked={confirmCheck3} inputReadOnly={true}>완전판매 확인서</Checkbox>
                        </Row>
                    </Card>
                </div>
                <div style={{width:"100%", textAlign : "center", margin : "3px 0px 2px 0px"}}>
                    <Button style={{width:"40%", textAlign : "center"}} type="primary" onClick={informCheck}>발송</Button>
                </div>
            </div>
        </LsySpin>
        </>
    );
}

export default InsuranceComp;