/**종료 */
import React, {useState, useEffect} from 'react';
import { Modal, WhiteSpace, Toast} from "antd-mobile";
import NavHeader from 'styleComponents/NavHeader';
import 'antd-mobile/dist/antd-mobile.css';
import * as service from 'service/post';
const alert = Modal.alert;
export default function FinanceFinish(props){
    const [successLoad, setSuccessLoad] = useState(false);
    const [userData, setUserData] = useState({});
    const alerText = {text: "확인"};

    useEffect(() => {
        console.log(props.location.state);
        try{
            if(!props.location.state.userData)
            {
                alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
            }else{
                setSuccessLoad(true);
                setUserData(props.location.state.userData);
                sendAgreeDate();
            }
        }catch(error){
            alert('알림',"잘못된 경로로 접근 하였습니다.",[alerText]);
        }
        console.log(userData);
    }, [])

    const sendAgreeDate = async() =>{
        let params = {
            url: '/Imo/MobileBigyo/SaveAgreeDate',
            pk : props.location.state.userData.userInfo.pk,
            gubun : 'finish_all_agree',
            imochk: 'ok',
        };
        Toast.loading("Loding...");
        let result = await service.getPost(params);
        Toast.hide();
        if(result.data.status.ercode !== '0'){
            alert('알림', result.data.status.ermsg, [alerText]);
        }
    }
    return (
        <>  
        {
            successLoad?
            <div>
                <NavHeader/>
                <WhiteSpace/>
                <WhiteSpace/>
                <div style={{padding : '0 15px'}}>                   
                    <div style={{textAlign : 'center', border: 'solid thin', backgroundColor : '#fffcf4'}}>
                    보험가입을 위한
                    <br/>
                    확인서 작성이 완료되었습니다.
                    <br/>
                    감사합니다.
                    </div>
                    <WhiteSpace/>
                   
                </div>
            </div>
            :
            <div>잘못된 접근</div>
        }
        </>
    )
};